import {IRectangle, Rectangle} from '../../business';

/**
 * Calculates the bounds of the drag.
 */
export function calculateImageBounds(
  crop: IRectangle,
  iW: number,
  iH: number,
  scale = 1
): Rectangle {
  let {x, y, width, height} = crop;

  /**
   * The transform origin defaults to the centre.
   * Hence we reverse the positional offset caused by the scale.
   */
  const sOffsetX: number = iW * (1 - scale) * 0.5;
  const sOffsetY: number = iH * (1 - scale) * 0.5;

  const wUnit: number = width - iW * scale;
  const hUnit: number = height - iH * scale;

  if (wUnit >= 0) {
    width = wUnit;
  } else {
    x = x + wUnit;
    width = -wUnit;
  }

  if (hUnit >= 0) {
    height = hUnit;
  } else {
    y = y + hUnit;
    height = -hUnit;
  }

  x -= sOffsetX;
  y -= sOffsetY;

  return new Rectangle({
    height: height,
    width: width,
    x: x,
    y: y,
  });
}
