import {DragConfig, PinchConfig, useGesture} from '@use-gesture/react';
import {ImageEditorCrop, ImageEditorCropProps} from './crop';
import {ImageEditorImage, SpringProps} from './image';

import React from 'react';
import {calculateImageBounds} from './image-bounds';
import styled from '@emotion/styled';
import {useResizeDetector} from 'react-resize-detector';
import {clamp, flerp, noop, Rectangle} from '../../business';

const ImageEditorDiv = styled.div`
  border: black 2px solid;
  position: relative;
  min-height: 500px;
  overflow: hidden;
  touch-action: none;
  > * {
    position: absolute;
    inset: 0;
    pointer-events: none;
  }
`;

export interface ImageEditorProps
  extends React.DetailedHTMLProps<
    React.HTMLAttributes<HTMLDivElement>,
    HTMLDivElement
  > {
  /**
   * The source of the image.
   */
  imageSrc?: string;

  /**
   * The position or scale to apply to the image.
   */
  imageTransform?: Partial<SpringProps> | null | undefined;

  /**
   * The event called when the position or scale of the image changes.
   */
  onChangeImageTransform?: (value: SpringProps) => void;

  /**
   * The properties of the cropper.
   */
  crop?: ImageEditorCropProps | null | undefined;

  /**
   * The negative bound of the image scale.
   */
  imageScaleMin?: number;

  /**
   * The positive bound of the image scale.
   */
  imageScaleMax?: number;
}

/**
 * A component which allows users to change the position
 * and scale of an image with some constraints.
 */
export const ImageEditor: React.FC<ImageEditorProps> = props => {
  /**
   * Automatically track the size of the image editor.
   */
  const imageEditorRef = React.useRef<HTMLDivElement>(null);
  const {width: imageEditorWidth = 0, height: imageEditorHeight = 0} =
    useResizeDetector<HTMLDivElement>({
      targetRef: imageEditorRef,
    });

  /**
   * Automatically track the size of the image editor.
   */
  const [imageSize, setImageSize] = React.useState<
    [width: number, height: number]
  >([0, 0]);

  const [imageWidth, imageHeight] = imageSize;

  const {
    children,
    imageScaleMin = 0,
    imageScaleMax = Infinity,
    onChangeImageTransform = noop,
    imageSrc,
    imageTransform,
    crop,
    ...other
  } = props;

  const cW: number = crop?.width ?? 500;
  const cH: number = crop?.height ?? 300;

  const DEFAULT_CROP_X: number = flerp(0, imageEditorWidth - cW);
  const DEFAULT_CROP_Y: number = flerp(0, imageEditorHeight - cH);

  const cX: number = crop?.x ?? DEFAULT_CROP_X;
  const cY: number = crop?.y ?? DEFAULT_CROP_Y;

  const {scale: iS = 1, rotateZ = 0} = imageTransform ?? {};

  const scale: number = clamp(iS, imageScaleMin, imageScaleMax);

  const iBounds: Rectangle = calculateImageBounds(
    {height: cH, width: cW, x: cX, y: cY},
    imageWidth,
    imageHeight,
    scale
  );

  const DEFAULT_IMAGE_X: number = flerp(iBounds.x, iBounds.width);
  const DEFAULT_IMAGE_Y: number = flerp(iBounds.y, iBounds.height);

  const {x: xUnconstrain = DEFAULT_IMAGE_X, y: yUnconstrain = DEFAULT_IMAGE_Y} =
    imageTransform ?? {};

  const iX: number = clamp(xUnconstrain, iBounds.x, iBounds.right);
  const iY: number = clamp(yUnconstrain, iBounds.y, iBounds.bottom);

  const useGestureDragConfiguration: DragConfig = {
    bounds: {
      bottom: iBounds.bottom,
      left: iBounds.x,
      right: iBounds.right,
      top: iBounds.y,
    },
    from: () => [iX, iY],
    rubberband: true,
  };

  const useGesturePinchConfiguration: PinchConfig = {
    rubberband: true,
    scaleBounds: {max: imageScaleMax, min: imageScaleMin},
  };

  useGesture(
    {
      onDrag: state => {
        state.event.preventDefault();

        const [offsetX, offsetY] = state.offset;
        if (state.pinching) {
          state.cancel();
          return;
        }

        onChangeImageTransform({
          rotateZ: rotateZ,
          scale: scale,
          x: offsetX,
          y: offsetY,
        });
      },
      onPinch: state => {
        state.event.preventDefault();

        const {
          origin: [ox, oy],
          first,
          movement: [ms],
          offset: [s, a],
        } = state;

        /**
         * F(x, s) = s * x
         * F(x, 1) = F(x, s) + offset
         * offset = x * (1 - s)
         */

        const {current} = imageEditorRef;
        if (first && current) {
          const tx = ox - flerp(cX, cW);
          const ty = oy - flerp(cY, cH);
          state.memo = [iX, iY, tx, ty];
        }

        const x = state.memo[0] - (ms - 1) * state.memo[2];
        const y = state.memo[1] - (ms - 1) * state.memo[3];

        onChangeImageTransform({
          rotateZ: a,
          scale: s,
          x: x,
          y: y,
        });

        return state.memo;
      },
    },
    {
      drag: useGestureDragConfiguration,
      eventOptions: {passive: false},
      pinch: useGesturePinchConfiguration,
      target: imageEditorRef,
    }
  );

  /**
   * Handle the image size change.
   */
  function handleImageSizeChange(w: number, h: number): void {
    const [currW, currH] = imageSize;
    if (w !== currW || h !== currH) {
      setImageSize([w, h]);
    }
  }

  return (
    <>
      <ImageEditorDiv draggable ref={imageEditorRef} {...other}>
        <ImageEditorImage
          src={imageSrc}
          x={iX}
          y={iY}
          scale={iS}
          rotateZ={rotateZ}
          onChangeSize={handleImageSizeChange}
        />

        <ImageEditorCrop
          stroke="#ffffff"
          strokeWidth={5}
          {...crop}
          x={cX}
          y={cY}
          width={cW}
          height={cH}
        />
        {!!children && <div id="image-editor-hud">{children}</div>}
      </ImageEditorDiv>
    </>
  );
};

export default ImageEditor;
