import {animated, useSpring} from '@react-spring/web';

import React from 'react';
import {useResizeDetector} from 'react-resize-detector';
import {noop} from '../../business';

export interface SpringProps {
  x: number;
  y: number;
  scale: number;
  rotateZ: number;
}

type I = React.DetailedHTMLProps<
  React.ImgHTMLAttributes<HTMLImageElement>,
  HTMLImageElement
>;

export interface ImageEditorImageProps extends SpringProps {
  src?: I['src'];
  alt?: I['src'];
  onChangeSize?: (width: number, height: number) => void;
}

/**
 * The image element which fits the image transform / bounds.
 */
export const ImageEditorImage: React.FC<ImageEditorImageProps> = props => {
  const {rotateZ, scale, x, y, src, alt, onChangeSize = noop} = props;

  const [spring] = useSpring<SpringProps>(
    {
      to: {rotateZ: rotateZ, scale: scale, x: x, y: y},
    },
    [rotateZ, scale, x, y]
  );

  const imageRef = React.useRef<HTMLImageElement>(null);

  const {width, height} = useResizeDetector<HTMLImageElement>({
    targetRef: imageRef,
  });

  React.useEffect(() => {
    if (!!width && !!height) {
      onChangeSize(width, height);
    }
  }, [height, onChangeSize, width]);

  return <animated.img style={spring} src={src} alt={alt} ref={imageRef} />;
};

export default ImageEditorImage;
