import React from 'react';
import {PartialRectangle} from '../../business';

export type ImageEditorCropProps = React.SVGProps<SVGRectElement> &
  PartialRectangle;

/**
 * The backdrop with a scissors mask hole in the middle / centre.
 */
export function ImageEditorCrop(props: ImageEditorCropProps) {
  return (
    <svg width="100%" height="100%">
      <mask id="cropmask">
        <rect x={0} y={0} width="100%" height="100%" fill="white" />

        <rect {...props} fill="black" strokeWidth={0} />
      </mask>

      <g mask="url(#cropmask)">
        <rect width="100%" height="100%" fill="black" fillOpacity={0.4} />

        <rect {...props} fill="white" />
      </g>
    </svg>
  );
}

export default ImageEditorCrop;
