import {flerp} from './clerp';
import {IRectangle, PartialRectangle, Rectangle} from './rectangle';

/**
 * Automatically tries to centre a rectangle
 * within the specified container.
 */
export function calculateDefaultPosition(
  rectangle: PartialRectangle,
  container: PartialRectangle
): IRectangle {
  const {x, y, width = 0, height = 0} = rectangle;
  const container_ = new Rectangle(container);

  const defaultX: number = flerp(container_.x, container_.width - width);
  const defaultY: number = flerp(container_.y, container_.height - height);

  const res: IRectangle = {
    height: height,
    width: width,
    x: x ?? defaultX,
    y: y ?? defaultY,
  };

  return res;
}
